<template>
    <div class="register">
    <b-container>
    <b-row class="mt-5 mx-auto">
      <b-col>
        <b-card v-if="waiting" header="Register" header-bg-variant="prime" header-text-variant="cream" header-tag="h3">
          <b-form @submit="onSubmit" @reset="onReset" v-if="show">

            <b-input-group class="my-4" label="Username:" label-for="exampleInput1">
                <b-input-group-prepend inline is-text>
                  <b-icon icon="person-fill"></b-icon>
                </b-input-group-prepend>
                <b-form-input
                            id="username"
                            v-model="form.username"
                            required
                            placeholder="Enter username">
                </b-form-input>
                <b-form-invalid-feedback :state="validName">
                  Your username must be 3-20 characters long.
                </b-form-invalid-feedback>
                <b-form-valid-feedback :state="validName">
                  Looks Good.
                </b-form-valid-feedback>
            </b-input-group>

            <b-input-group class="my-4" label="Email address:" label-for="exampleInput4">
              <b-input-group-prepend inline is-text>
                  <b-icon icon="envelope"></b-icon>
                </b-input-group-prepend>
                <b-form-input
                            type="email"
                            v-model="form.email"
                            required
                            placeholder="Enter email">
                </b-form-input>
            </b-input-group>

            <b-input-group class="my-4" label="Vocab:" label-for="exampleInput7">
              <b-input-group-prepend inline is-text>
                  <b-icon icon="card-list"></b-icon>
                </b-input-group-prepend>
                <b-form-select id="vocab"
                required
                :options="vocabs"
                v-model="form.vocab"
                >
                </b-form-select>
            </b-input-group>

            <b-input-group id="password" label="Password:" label-for="exampleInput2">
                <b-input-group-prepend inline is-text>
                  <b-icon icon="lock-fill"></b-icon>
                </b-input-group-prepend>
                <b-form-input id="exampleInput2"
                            type="password"
                            v-model="form.password"
                            required>
                </b-form-input>
            </b-input-group>

            <b-input-group class="my-4" id="exampleInputGroup3" label="Confirm Password:" label-for="exampleInput3">
                <b-input-group-prepend inline is-text>
                  <b-icon icon="lock-fill"></b-icon>
                </b-input-group-prepend>
                <b-form-input id="exampleInput3"
                            type="password"
                            v-model="form.confirm"
                            required>
                </b-form-input>
                <b-form-invalid-feedback :state="validPass">
                  You must enter the same password
                </b-form-invalid-feedback>
            </b-input-group>

            <div class="d-flex justify-content-between">
                <div>
                <button class="buttonDiv bg-second px-3" style="width:140px" type="submit"> <b-icon-forward variant="cream" font-scale="1.5"></b-icon-forward></button>
                &nbsp;&nbsp;
                <button class="buttonDiv bg-alert px-3" style="width:60px" type="reset"> <b-icon-x-circle variant="cream" font-scale="1.5"></b-icon-x-circle></button>
                </div>
            </div>
          </b-form>
        </b-card>
        <div v-else align="center">
            <b-icon icon="three-dots" animation="cylon" variant="prime" font-scale="6"></b-icon>
        </div>
      </b-col>
    </b-row>
  </b-container>

  </div>

</template>

<script>
import router from '../router/index'

export default {
  name: 'app',
  data () {
    return {
      form: {
        username: '',
        email: '',
        vocab: '',
        password: '',
        confirm: ''
      },
      show: true,
      waiting: true,
      vocabs: [
        {text: 'ESP Tourism 1', value: 'tourism'},
        {text: 'ESP Food (Vietnamese)', value: 'food'}
      ]
    }
  },
  computed: {
    validName () {
      return this.form.username.length > 2 && this.form.username.length < 20
    },
    validPass () {
      return this.form.password === this.form.confirm
    }
  },
  methods: {
    onSubmit (evt) {
      evt.preventDefault()
      if (this.validName && this.validPass) {
        this.register()
        // alert(JSON.stringify(this.form))
      } else {
        alert('form is not complete')
      }
    },
    onReset (evt) {
      evt.preventDefault()
      /* Reset our form values */
      for (let item in this.form) {
        this.form[item] = ''
      }
      /* Trick to reset/clear native browser form validation state */
      this.show = false
      this.$nextTick(() => { this.show = true })
    },
    register () {
      this.waiting = false
      let _this = this
      this.$store.dispatch('register', { userData: this.form })
        .then(function (response) {
          if (response.err) {
            alert(response.msg)
            _this.waiting = true
          } else {
            alert(response.msg)
            router.push('/login')
          }
        })
    }
  }
}
</script>



